import React, {useState, useContext, useEffect} from 'react'
import axios from 'axios'
import {GlobalState} from '../../../GlobalState'
import Loading from '../utils/loading/Loading'
import { useHistory, useParams } from 'react-router-dom'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary';

const initialState = {
    product_id: '',
    title: '',
    price: 0,
    description: 'How to and tutorial videos of cool CSS effect, Web Design ideas,JavaScript libraries, Node.',
    content: 'Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.',
    category: '',
    _id: ''
}
function CreateProduct() {
    const state = useContext(GlobalState)
    console.log(state)
    const [product, setProduct] = useState(initialState)
    const [categories] = state.categoriesAPI.categories
    const [images, setImages] = useState(false)
    const [loading, setLoading] = useState(false)


    const [isAdmin] = state.userAPI.isAdmin
    const [token] = state.token

    const history = useHistory()
    const param = useParams()

    const [products] = state.productsAPI.products
    const [onEdit, setOnEdit] = useState(false)
    const [callback, setCallback] = state.productsAPI.callback

    useEffect(() => {
        console.log(param.id)
        if(param.id){
            setOnEdit(true)
            products.forEach(product => {
                if (product._id === param.id) {
                    console.log(product)
                    setProduct(product)
                    setImages(product.images)
                }
            })
        }else{
            setOnEdit(false)
            setProduct(initialState)
            setImages(false)
        }
    }, [param.id, products])

    const handleUpload = async e =>{
        e.preventDefault()
        try {
            if(!isAdmin) return alert("You're not an admin")
            const file = e.target.files[0]
            
            if(!file) return alert("File not exist.")

            if(file.size > 1024 * 1024) // 1mb
                return alert("Size too large!")

            if(file.type !== 'image/jpeg' && file.type !== 'image/png') // 1mb
                return alert("File format is incorrect.")

            let formData = new FormData()
            formData.append('file', file)

            setLoading(true)
            const res = await axios.post('/api/upload', formData, {
                headers: {'content-type': 'multipart/form-data', Authorization: token}
            })
            setLoading(false)
            setImages(res.data)

        } catch (err) {
            alert(err.response.data.msg)
        }
    }

    const handleDestroy = async () => {
        try {
            if(!isAdmin) return alert("You're not an admin")
            setLoading(true)
            await axios.post('/api/destroy', {public_id: images.public_id}, {
                headers: {Authorization: token}
            })
            setLoading(false)
            setImages(false)
        } catch (err) {
            alert(err.response.data.msg)
        }
    }

    const handleChangeInput = e =>{
        const {name, value} = e.target
        setProduct({...product, [name]:value})
    }

    const handleSubmit = async e =>{
        e.preventDefault()
        try {
            if(!isAdmin) return alert("You're not an admin")
            if(!images) return alert("No Image Upload")

            if(onEdit){
                await axios.put(`/api/products/${product._id}`, {...product, images}, {
                    headers: {Authorization: token}
                })
            }else{
                await axios.post('/api/products', {...product, images}, {
                    headers: {Authorization: token}
                })
            }
            setCallback(!callback)
            history.push("/")
        } catch (err) {
            alert(err.response.data.msg)
        }
    }

    const styleUpload = {
        display: images ? "block" : "none"
    }
    return (
        <div className="create_product">
            <div className="upload">
                <input type="file" name="file" id="file_up" onChange={handleUpload}/>
                {
                    loading ? <div id="file_img"><Loading /></div>

                        : <div id="file_img" style={styleUpload}>
                            {images ? <img src={images.url}/> : <PhotoLibraryIcon/>}
                        <span onClick={handleDestroy}>X</span>
                    </div>
                }
                
            </div>

            <form onSubmit={handleSubmit}>
                <div className="row">
                    <TextField
                        fullWidth id="outlined-basic"
                        label="Product Id"
                        variant="outlined"
                        name="product_id"
                        type="number" required
                        value={product.product_id}
                        onChange={handleChangeInput}
                        disabled={onEdit} />
                </div>

                <div className="row">
                    <TextField
                        fullWidth id="outlined-basic"
                        label="Title"
                        variant="outlined"
                        name="title"
                        required
                        value={product.title}
                        onChange={handleChangeInput} />
                    
                </div>

                <div className="row">
                    <TextField fullWidth
                        id="outlined-basic"
                        label="Price"
                        variant="outlined"
                        name="price"
                        type="number"
                        required
                        value={product.price}
                        onChange={handleChangeInput} />
                    
                </div>

                <div className="row">
            
                    
                    <TextField
                        fullWidth
                        id="outlined-multiline-static"
                        label="Description"
                        multiline
                        rows={5}
                        variant="outlined" value={product.description} onChange={handleChangeInput}
                        type="text" name="description"
        />
                </div>

                <div className="row">
                    
                    <TextField
                        fullWidth
                        name="content"
                        id="outlined-multiline-static"
                        label="Content"
                        multiline
                        rows={7}
                        variant="outlined"
                        required
                        value={product.content}  onChange={handleChangeInput}
                    />
                </div>

                <div className="row">


                    <TextField
                        fullWidth
                        name="category"
                        id="standard-select-currency"
                        select
                        label="Category"
                        value={product.category}
                        onChange={handleChangeInput}
                        helperText="Please select a category"
        >
                    {categories.map((category) => (
                        <MenuItem key={category._id} value={category._id}>
                        {category.name}
                        </MenuItem>
                    ))}
                    </TextField>
                    
                </div>

                <button type="submit">{onEdit? "Update" : "Create Product"}</button>
            </form>
        </div>
    )
}

export default CreateProduct
